.card{
    max-width:450px;
    position:relative;
}

.hit{
    position:absolute;
    background:#ff4141;
    color:#fff;
    padding:10px;
    top:2px;
    left:3px;
}

.card__photo{
    width:100%;
    height:676px;
}

.card__info{
    padding:10px;
}

.card__name{
    text-align:center;
    font-size:20px;
    margin-bottom:10px;
}

.card__price{
    font-weight: 700;
    font-size: 24px;
    text-align:center;
    margin-bottom:20px;
}

.card__btn{
    display:flex;
    align-items:center;
    width:100%;
    background:#000;
    color:#fff;
    justify-content: center;
    gap:20px;
    padding:10px 0px;
    border-radius:20px;
    cursor:pointer;
    transition: all .3s ease-in-out;
}

.card__btn:hover{
    color:#000;
    background:#fff;
}

.card__info{
    display:flex;
    flex-direction:column;
    justify-content: space-between;
}