.btn-big{
    text-transform:capitalize;
    background:none;
    border:none;
    width:400px;
    background:#000;
    color:#fff;
    font-size:20px;
    padding:10px 0px;
    border-radius:20px;
    font-weight:700;
    cursor:pointer;
    transition: all .3s ease-in-out;
}

.btn-big:hover{
    background:#fff;
    color:#000;
}