.item{
    max-width:219px;

}

.item-top{
    position:relative;
    height:365px;
}

.item__img{
    width:100%;
    height:100%;
    object-fit: cover;
}

.item-page{
    position: absolute;
    top:0px;
    left:1px;
    padding:3px 11px;
    background:#ee4a2e;
    color:#fff;
    font-size:12px;
}

.item__bottom{
    padding:5px 20px;
}

.item__name{
    font-size:16px;
    font-weight:700;
    margin-bottom:10px;
}

.item__raitind{
    width:200px;
    height:30px;
    object-fit:cover;
    
}
.item__price{
    display:flex;
    padding:5px 0px;
    gap:20px;
}

.item__old-price{
    font-size:12px;
    text-decoration: line-through;
}
.item__new-price{
    color:#e61b1b;
}