.cartItem{
    margin:100px 170px;
}

.cartItem hr{
    height:3px;
    background:#010101;
    border:none;
}

.cartItem-format-main{
    display:grid;
    grid-template-columns: .5fr 2fr 1fr 1fr 1fr 1fr;
    align-items:center;
    gap:70px;
    padding:20px 0px;
    font-size:18px;
    font-weight:600;
}

.cartItem__close{
    width:40px;
    height:40px;
    cursor:pointer;
}

.catIcon-product-icon{
    width:300px;
    height:300px;
}