.header{
    display:flex;
    align-items:center;
    justify-content: space-between;
    padding:10px 20px;
    border-bottom:1px solid #000;
    box-shadow: 2px 0px 3px #000;
}

.logo{
    display:flex;
    align-items:center;
    text-decoration:none;
}

.logo__img{
    width:80px;
    height:80px;
}

.logo__text{
    font-size:18px;
    color:#000;
    
}
.header__right{
    display:flex;
    align-items:center;
    gap:20px;
}
.soc-links{
    display:flex;
    align-items:center;
    gap:20px;
}

.soc-links__img{
    width:20px;
    height:20px;
    fill:#ff7a22;
    transition: fill .3s ease-in-out;
}

.soc-links__img:hover{
    fill:#7e7c7c;
}

.enter,
.basket{
    border:none;
    background:none;
    cursor:pointer;
}

.enter__img,
.card__img{
    fill:#fa6501;

}