.subscribe{
    padding:50px 0px;
}

.subscribe__block{
    padding:30px 0px;
    width:100%;
    background:#000;
    color:#fff;
}
.subscribe__content{
    width:740px;
    margin:0 auto;
}

.subscribe__title{
    font-size:30px;
    margin-bottom:30px;
}

.subscribe__subtitle{
    font-weight:400;
    font-size:22px;
    margin-bottom: 20px;
}

.subscribe__text{
    font-size:15px;
    margin-bottom:10px;
}

.subscribe__lable{
    font-size:11px;
}

.subscribe__input{
    width:100%;
    padding:10px;
    font-size:20px;
    color:#ccc;
}

.subscribe__input::placeholder{
    font-size:20px;
    color:#ccc;
}

.subscribe__btns{
    padding:20px 0px;
    display:flex;
    align-items:center;
    gap:40px;
}


.subscribe__btn{
    width:50%;
    background:#fff;
    color:#000;
    padding:12px 0px;
    border-radius:20px;
    cursor: pointer;
    transition: all .3s ease-in-out;
}

.subscribe__btn:hover{
    background: #000;
    color:#fff;
}


