.product-display{
    padding: 40px 170px;
    display:flex;
    gap:80px;
}

.product-display-left{
    display:flex;
    /* align-items:center; */
    gap:100px;
}

.product-display__img-list{
    display:flex;
    flex-direction:column;
    align-items:center;
    gap:40px;
}

.product-display__img{
    width:100px;
    height:100px;
}

.product-display__title{
    padding:14px 0px;
    font-size:18px;
}

.sale{
    display:flex;
    align-items: center;
    justify-content:space-between;
    padding:10px;
}

.product-display__prices{
    font-size:30px;
    padding:20px 0px;
}

.product-display__description{
    padding:15px 0px;
    font-size:18px;
}

.product-display__sizes{
    padding:20px 0px;
}

.product-display__btn{
    width:300px;
    background:#000;
    color:#fff;
    font-size:20px;
    cursor:pointer;
    transition: all .3s ease-in-out;
}

.product-display__btn:hover{
    background:#fff;
    color:#000;
}