.signup{
    padding:50px 0px;
}

.sigup__container{
    width:570px;
    margin:0 auto;
    border:1px solid #707070;
    padding:20px;
}

.signup__title{
    text-align:center;
    margin-bottom:40px;
    color: #ff7a22;
}

.signup__inputs{
    display:flex;
    flex-direction:column;
    gap:30px;
    padding:10px 0px;
}

.signup__input{
    padding:12px 0px;
}

.signup__btn{
    display:block;
    width:300px;
    background:#000;
    color:#fff;
    font-size:20px;
    margin: 0 auto;
    cursor:pointer;
}

.signup__logup{
    padding:20px 0px;
    text-align:center;
}