.footer{
    padding:20px;
    background:#000;
    color:#fff;
}

.footer-top{
    display:flex;
    justify-content: space-between;
}

.footer__links{
    list-style: none;
    margin:0px;
    display:flex;
    flex-direction: column;
    gap:20px;
}

.footer__contacts{
    display:flex;
    flex-direction:column;
    gap:10px;
}

.footer__num{
    font-size:18px;
}

.footer__link{
    text-decoration: none;
    color:#fff;
    font-size:18px;

}

.footer-bottom{
    padding:20px 0px;
    display:flex;
    gap:40px;
}

.app-link__img{
    width:37px;
    height:37px;
    border-radius: 50%;
}

.app-link{
    display:flex;
    align-items:center;
    gap:20px;
}