.banner{
    padding:50px 0px;
}

.banner__content{
    width:1000px;
    padding:80px 0px;
    margin:0 auto;
    background:url(../Assets/cloth-bg.webp);
    background-size:cover;
    background-position:center center;
    display:flex;
    align-items:center;
    flex-direction:column;
    justify-content: center;
}

.banner__title{
    font-size:40px;
    color:#fff;
    text-shadow: 0 0 5px #000;
    margin-bottom:20px;
}