.nav{
    display:flex;
    justify-content:flex-end;
    padding:20px 0px;
}

.nav__list{
    list-style:none;
    padding:0px;
    margin:0px;
    display:flex;
    align-items:center;
    gap:40px;
    padding-right:20px;
}

.nav__link{
    text-decoration:none;
    font-size:24px;
    font-weight:600;
    text-transform:capitalize;
    color:#ff7a22;
    transition: color .3s ease-in-out;
}
.nav__link:hover{
    color:#000;
}

.nav__link:active{
    color:#a59c9c;
}